import { CONTROLLER_KEY } from './constants';

/**
 * @name uiBbFilterFormActionsComponent
 * @type {object}
 *
 * @property {Object} filterParams          Filter form parameters
 * @property {Labels} buttonLabels          Texts used in component
 * @property {Object} searchFilterForm      Filter form object
 *
 * @property {Function} onFilter Function that calls on submitting the filter form
 * @property {Function} onClose Function that calls on closing the filter form
 * @property {Function} onClear Function that calls on clearing the filter form
 */

/**
 * Labels type definition
 * @typedef {Object} Labels
 * @property {String} apply             Accessibility label for apply button
 * @property {String} closePanel        Accesibility label for the close button
 * @property {String} clearAll          Accesibility label for clear button
 */

const uiBbFilterFormActionsComponent = {
  controller: CONTROLLER_KEY,
  bindings: {
    filterParams: '<',
    buttonLabels: '<',
    searchFilterForm: '<',
    onFilter: '&',
    onClose: '&',
    onClear: '&',
  },
  template: ` 
    <div class="card-footer card-footer-darker">
      <div class="row">
        <div class="col-12">
          <div class="d-none d-lg-block text-right">
            <button 
              type="button"
              class="btn btn-link"
              data-ng-show="$ctrl.searchFilterForm.$dirty"
              data-ng-click="$ctrl.onClearFilter($ctrl.searchFilterForm)"
              title="{{ $ctrl.buttonLabels.clearAll }}"
              data-role="filter-form-actions-clear-all">
              {{ $ctrl.buttonLabels.clearAll }}
            </button>
            <button
              type="button" 
              class="btn btn-secondary"
              data-ng-click="$ctrl.onClose(ext)"
              title="{{ $ctrl.buttonLabels.closePanel }}"
              data-role="filter-form-actions-cancel">
              {{ $ctrl.buttonLabels.closePanel }}
            </button>
            <button
              data-ng-disabled="$ctrl.isFormValid($ctrl.searchFilterForm)"
              class="btn btn-primary"
              title="{{ $ctrl.buttonLabels.apply }}"
              data-role="filter-form-actions-apply">
              {{ $ctrl.buttonLabels.apply }}
            </button>
          </div>
          <div class="d-lg-none d-block">
            <button
              data-ng-disabled="$ctrl.isFormValid($ctrl.searchFilterForm)"
              class="btn btn-primary btn-block"
              title="{{ $ctrl.buttonLabels.apply }}"
              data-role="filter-form-actions-apply">
              {{ $ctrl.buttonLabels.apply }}
            </button>
            <button
              type="button" 
              class="btn btn-secondary btn-block"
              data-ng-click="$ctrl.onClose(ext)"
              title="{{ $ctrl.buttonLabels.closePanel }}"
              data-role="filter-form-actions-cancel">
              {{ $ctrl.buttonLabels.closePanel }}
            </button>
            <button 
              type="button"
              class="btn btn-link btn-block"
              data-ng-show="$ctrl.searchFilterForm.$dirty"
              data-ng-click="$ctrl.onClearFilter($ctrl.searchFilterForm)"
              title="{{ $ctrl.buttonLabels.clearAll }}"
              data-role="filter-form-actions-clear-all">
              {{ $ctrl.buttonLabels.clearAll }}
            </button>
          </div>
        </div>
      </div>
    </div>
  `,
};

export default uiBbFilterFormActionsComponent;



