/**
 * @module ui-bb-filter-form-actions-ng
 *
 * @description
 * Filter form actions component
 *
 * @example
 * // In an extension:
 * // file: scripts/index.js
 * import uiBbFilterFormActionsKey from 'ui-bb-filter-form-actions-ng';
 *
 * export const dependencyKeys = [
 *   uiBbFilterFormActionsKey,
 * ];
 *
 * // file: templates/template.ng.html
 * <ui-bb-filter-form-actions-ng
 *   data-filter-params="$ctrl.filterParams"
 *   data-search-filter-form="searchFilterForm"
 *   data-on-close="(ext) => {}"
 *   data-on-filter="(form) => {}"
 *   data-on-clear="() => {}"
 *   data-button-labels="{
 *     apply: 'Apply',
 *     closePanel: 'Close panel,
 *     clearAll: 'Clear all'
 *   }">
 * </ui-bb-filter-form-actions-ng>
 */

import angular from 'vendor-bb-angular';

import { COMPONENT_KEY, CONTROLLER_KEY, MODULE_KEY } from './constants';

import component from './component';
import controller from './controller';

/**
 * @name default
 * @type {string}
 * @description The angular module name
 */
export default angular
  .module(MODULE_KEY, [])
  .component(COMPONENT_KEY, component)
  .controller(CONTROLLER_KEY, ['$timeout', controller])
  .name;



