import angular from 'vendor-bb-angular';

/**
 * @name uiBbFilterFormActionsController
 * @type {function}
 *
 * @description
 * Filter form controller
 *
 * @param {Promise} $timeout Angular timeout function
 */
export default function uiBbFilterFormActionsController($timeout) {
  /**
   * @name uiBbFilterFormActionsController
   * @ngkey uiBbFilterFormActionsController
   * @type {object}
   * @description Filter form controller
   */
  const $ctrl = this;

  /**
   * @name immutableFilter
   * @type {object}
   * @description Filter parameters in reset state
   */
  let immutableFilter;

  /**
  * @description
  * Reset form state method.
  *
  * @inner
  * @name uiBbFilterFormActionsController#resetFormState
  * @type {function}
  */
  const resetFormState = form => {
    if (form) {
      form.$setUntouched();
      form.$setPristine();
    }
  };

  /**
   * @name uiBbFilterFormActionsController#getClearedFilterParams
   * @inner
   *
   * @description
   * Get pristine state of filter form model
   *
   * @type {function}
   *
   * @param filterObject
   */
  const getClearedFilterParams = filterObject => Object.keys(filterObject).reduce((object, key) => {
    const value =
      (filterObject[key] instanceof Object && filterObject[key].constructor === Object) ?
      getClearedFilterParams(filterObject[key]) : null;

    return Object.assign(object, { [key]: value });
  }, {});

  /**
   * @description
   * Reset filter parameters.
   *
   * @name uiBbFilterFormActionsController#onClearFilter
   * @type {function}
   */
  const onClearFilter = (form) => {
    Object.assign($ctrl.filterParams, angular.copy(immutableFilter));

    $ctrl.onFilter(form);
    $ctrl.onClear();
    resetFormState(form);
  };

  /**
   * @name uiBbFilterFormActionsController#$onInit
   *
   * @description
   * Angular $onInit lifecycle hook
   *
   * @type {function}
   */
  const $onInit = () => {
    immutableFilter = getClearedFilterParams($ctrl.filterParams);

    $timeout(() => {
      $ctrl.searchFilterForm.$setUntouched();
      $ctrl.searchFilterForm.$setPristine();
    });
  };

  /**
   * @description
   * Check if form state is pristine or invalid.
   *
   * @public
   * @name uiBbFilterFormActionsController#isFormValid
   * @type {function}
   */
  const isFormValid = (form) => (!form || form.$pristine || form.$invalid);

  Object.assign($ctrl, {
    isFormValid,
    resetFormState,
    onClearFilter,
    /* lifecycle hooks */
    $onInit,
  });
}



